import request from "@/utils/request";


export const getList = (params) =>{
  return request({
    url: '/bdxx-api/chip/v3/rich/search',
    method: 'get',
    params: params
  })
}

export const download = (url) =>{
  return request({
    url: url,
    method: 'get',
    responseType: 'blob'
  })
}