<template>
  <div style="width: 1024px; margin: 0 auto">
    <n-input-group style="margin-bottom: 20px">
      <n-input v-model:value="data.model" aria-placeholder="芯片名" />
      <n-button type="primary" ghost @click="search"> 搜索 </n-button>
    </n-input-group>
    <div v-if="searching">
      <n-skeleton text :repeat="2" />
      <n-skeleton text style="width: 60%" />
    </div>

    <n-list hoverable clickable>
      <n-list-item v-for="(item, index) in pageList" :key="index">
        <div style="display: flex; align-items: center; justify-content: space-between">
          <n-thing :title="item.name" content-style="margin-top: 10px;">
            <template #description>
              <n-space size="small" style="margin-top: 4px">
                <n-tag :bordered="false" type="info" size="small"> {{ item.brand_name }} </n-tag>
                <n-tag :bordered="false" type="info" size="small"> {{ item.descri }} </n-tag>
              </n-space>
              <n-space size="small" style="margin-top: 4px">
                {{
                  'https://www.semiee.com/file/' +
                  item.brand_name.split('-')[1] +
                  '/' +
                  item.brand_name.split('-')[1] +
                  '-' +
                  item.name +
                  '.pdf'
                }}
              </n-space>
            </template>
          </n-thing>
          <div>
            <n-button type="primary" style="margin-right: 10px" @click="gotoDetail(item)"> 打开 </n-button>
            <n-button type="info" @click="toDownload(item)"> 下载 </n-button>
          </div>
        </div>
      </n-list-item>
    </n-list>
    <n-divider dashed> 接口最多查询50条数据，这不怪我。 </n-divider>
  </div>
</template>

<script setup>
import { getList, download } from '@/api/index';
import { ref } from 'vue';

const pageList = ref([]);
const page = ref(1);
const searching = ref(false);

const data = ref({
  pageIndex: 1,
  pageNum: 10,
  model: '',
});

const search = async () => {
  searching.value = true;
  const res = await getList(data.value);
  searching.value = false;
  console.log(res);
  pageList.value = res.result;
};
const gotoDetail = (item) => {
  const url =
    'https://www.semiee.com/file/' + item.brand_name.split('-')[1] + '/' + item.brand_name.split('-')[1] + '-' + item.name + '.pdf';
  window.open(url);
};
const toDownload = async (item) => {
  const url = '/file/' + item.brand_name.split('-')[1] + '/' + item.brand_name.split('-')[1] + '-' + item.name + '.pdf';
  const res = await download(url);
  let dom = window.URL.createObjectURL(res);
  let link = document.createElement('a');
  link.style.display = 'none';
  link.href = dom;
  link.setAttribute('download', item.brand_name.split('-')[1] + '-' + item.name +'excel.pdf');
  document.body.appendChild(link);
  link.click();
};
</script>
